	module.exports = {"data":{"site":{"siteMetadata":{"startDate":"2018-06-06","lessons":8,"noClass":[4]}},"markdownRemark":{"html":"<p>Float is CSS property that is used to display content. Floats where originally intended to get the magazine feel on the web by allowing images or other content to wrap around text within an article. Over time floats have been used for other reason like layouts. CSS floats can be applied to any elements, both <em>inline</em> and <em>block</em>.</p>\n<p>When an element is floated they go into a state that is not inline or block. A floated element is alot like <code class=\"language-text\">display: inline-block</code> but the content will wrap around it.. A floated element differs from all other elements because it is not part of the <em>flow of the document</em> so the container is not able to use a floated element to determine its height.</p>\n<p><strong>Note:</strong> Elements are floated horizontally, this means that an element can only be floated left or right, not up or down.</p>\n<h2>Float Basics</h2>\n<table>\n<thead>\n<tr>\n<th>prop</th>\n<th>value</th>\n<th>desc</th>\n<th>example</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>float</td>\n<td>left</td>\n<td>float an item to the left and content will wrap around it</td>\n<td><code class=\"language-text\">float: left;</code></td>\n</tr>\n<tr>\n<td>float</td>\n<td>right</td>\n<td>float an item to the right and content will wrap around it</td>\n<td><code class=\"language-text\">float: right;</code></td>\n</tr>\n</tbody>\n</table>\n<iframe height='1069' scrolling='no' title='Floats Explained' src='//codepen.io/danhahn/embed/NgyEJG/?height=1069&theme-id=light&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='https://codepen.io/danhahn/pen/NgyEJG/'>Floats Explained</a> by Dan Hahn (<a href='https://codepen.io/danhahn'>@danhahn</a>) on <a href='https://codepen.io'>CodePen</a>.\n</iframe>\n<p><strong>Note:</strong> Elements that are floated will move to the far left or right of the container and the content will wrap around the floated element.</p>\n<p>When two elements are floated in the same direction one after another they will \"stack\" next to each other.</p>\n<h2>Clearing A float</h2>\n<p>When an element is floated it affects both the element that has the float and the element after it. To stop an element from being affected by the float you need to add the css property of clear.</p>\n<p>Clear has three values, left, right and both. <code class=\"language-text\">clear: left</code> will clear a <code class=\"language-text\">float: left</code>, <code class=\"language-text\">clear: right</code> will clear a <code class=\"language-text\">float right</code> and <code class=\"language-text\">clear: both</code> will clear either a left or right float.</p>\n<table>\n<thead>\n<tr>\n<th>prop</th>\n<th>value</th>\n<th>desc</th>\n<th>example</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>clear</td>\n<td>left</td>\n<td>A clear \n<strong>left</strong>\n will clear an element that has been floated left</td>\n<td><code class=\"language-text\">clear: left;</code></td>\n</tr>\n<tr>\n<td>clear</td>\n<td>right</td>\n<td>A clear \n<strong>right</strong>\n will clear an element that has been floated right</td>\n<td><code class=\"language-text\">clear: right;</code></td>\n</tr>\n<tr>\n<td>clear</td>\n<td>both</td>\n<td>A clear \n<strong>both</strong>\n will clear an element that has been floated left \n<strong>or</strong>\n right</td>\n<td><code class=\"language-text\">clear: both;</code></td>\n</tr>\n</tbody>\n</table>\n<p>The the clear float must be added to an element after the floated element.</p>\n<p>In this example the content in the second element is not affected by the the float because it has a clear:left; on it.</p>\n<p><strong>Note:</strong> When the clearing an element it will clear that element and all the elements after.</p>\n<h2>Stacking Floats</h2>\n<p>When two or more consecutive elements are floated in the same direction they will \"stack\" horizontally. This is often used to create navigation or layouts.</p>\n<p><strong>Note:</strong> when all the content with in an element is floated that element will seem to have no height.</p>\n<h3>Example</h3>\n<iframe height='514' scrolling='no' title='Stacking Floats' src='//codepen.io/danhahn/embed/dRdrRm/?height=514&theme-id=light&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='https://codepen.io/danhahn/pen/dRdrRm/'>Stacking Floats</a> by Dan Hahn (<a href='https://codepen.io/danhahn'>@danhahn</a>) on <a href='https://codepen.io'>CodePen</a>.\n</iframe>\n<p><a href=\"http://css-tricks.com/all-about-floats/\">More Info</a></p>\n<script src=\"lesson-6.js\"></script>\n<style>\n  table td:nth-child(4) {\n    white-space: nowrap;\n  }\n</style>","frontmatter":{"lesson":"Floats","title":"Lesson 5","lessonId":5,"downloads":null}},"allMarkdownRemark":{"edges":[{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"CSS Box Model"},"fields":{"slug":"/5/index.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/pseudo.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"Pseudo class"},"fields":{"slug":"/5/pseudo.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/floats.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"Floats"},"fields":{"slug":"/5/floats.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/5/clear-fix.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 5","lesson":"Clear fix"},"fields":{"slug":"/5/clear-fix.html"}}}]}},"pathContext":{"slug":"/5/floats.html","title":"Lesson 5"}}

